<template>
  <div>
    <!-- loading  -->
    <loading :active.sync="isLoading"></loading>
    <div class="content mt-5">
      <div class="table-responsive">
        <table class="table">
          <thead>
            <!-- width  -->
            <th width="200"></th>
            <th>E-mail</th>
            <th width="300"></th>
            <th width="150"></th>
            <th width="100" class="text-center"></th>
          </thead>
          <tbody>
            <!-- eslint v-for -->
            <tr v-for="(item) in orders" :key="item.id">
              <td >{{ item.create_at | timeFormat }}</td>
              <td>{{ item.user.user.email }}</td>
              <td><div  v-for="obj in item.products" :key="obj.id">{{ obj.product.title }} | <span class="text-info">{{ obj.qty}} {{obj.product.unit }}</span></div></td>
              <td class="text-right">{{ item.total | currency }}</td>
              <td class="text-center">
                 <!-- is_enableddata -->
                <span v-if="item['is_paid']" class="text-success"></span>
                <span v-else></span>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
    <Pagination class="d-flex justify-content-center" :pageData="pagination" @changepage="getOrders"></Pagination>
  </div>
</template>

<script>
import Pagination from '../../views/Pagination'
export default {
  components: {
    Pagination
  },
  data () {
    return {
      orders: [],
      isLoading: false,
      pagination: {}
    }
  },
  methods: {
    // serverdata
    getOrders (page = 1) { // ES6 
      let vm = this
      let api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/orders?page=${page}`
      // Loading
      vm.isLoading = true
      this.$http.get(api).then((response) => {
        console.log('orderList', response.data)
        window.scrollTo(0, 0)
        vm.orders = response.data.orders
        // pagination
        vm.pagination = response.data.pagination
        // loading
        vm.isLoading = false
      })
    }
  },
  created () {
    const vm = this
    vm.getOrders()
    // on==> pagination
    vm.$bus.$on('page:current', (currentPage) => {
      vm.getOrders(currentPage)
    })
  }
}
</script>
<style scoped>
.content {
  min-height: calc(100vh - (51px + 62px))
}
.table {
  min-width: 1000px;
}
</style>



