<template>
  <div>
    <!-- loading  -->
    <loading :active.sync="isLoading"></loading>
    <div class="content mt-5">
      <div class="create-btn-Pos">
        <button class="btn btn-primary mt-4" @click.prevent="openModal(true)"></button>
      </div>
      <div class="table-responsive">
        <table class="table mt-4">
          <thead>
            <!-- width  -->
            <th></th>
            <th></th>
            <th width="120"></th>
            <th width="120"></th>
            <th width="100"></th>
            <th width="80"></th>
            <th width="80"></th>
          </thead>
          <tbody>
            <!-- eslint v-for -->
            <tr v-for="(item) in products" :key="item.id">
              <td >{{ item.category }}</td>
              <td><img class="small rounded" :src="item.imageUrl" alt=""><div>{{ item.title }}</div></td>
              <td class="text-right">{{ item['origin_price'] | currency }}</td>
              <td class="text-right">{{ item.price | currency }}</td>
              <td>
                <!-- is_enableddata -->
                <span v-if="item['is_enabled']" class="text-success"></span>
                <span v-else></span>
              </td>
              <td><button class="btn btn-outline-primary btn-sm" @click.prevent="openModal(false, item)"></button></td>
              <td><button class="btn btn-danger btn-sm"  @click.prevent="openDelModal(item)"></button></td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
    <Pagination class="d-flex justify-content-center" :pageData="pagination" @changepage="getProducts"></Pagination>
    <!-- Modal -->
    <div class="modal fade" id="productModal" tabindex="-1" role="dialog"
        aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-dark text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span>{{ modalHead }}</span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="col-sm-4">
                <div class="form-group">
                  <label for="image"></label>
                  <input type="text" class="form-control" id="image"
                    placeholder="" v-model="tempProduct.imageUrl">
                </div>
                <div class="form-group" >
                  <label for="customFile"> 
                    <!-- loading icon -->
                    <i class="fas fa-spinner fa-spin" v-if="status.fileUploading"></i>
                  </label>
                  <!-- uploadFile -->
                  <input type="file" id="customFile" class="form-control"
                    ref="files" @change="uploadFile">
                </div>
                <img img="https://images.unsplash.com/photo-1483985988355-763728e1935b?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=828346ed697837ce808cae68d3ddc3cf&auto=format&fit=crop&w=1350&q=80"
                    class="img-fluid" alt="" :src="tempProduct.imageUrl">
              </div>
              <div class="col-sm-8">
                <div class="form-group">
                  <label for="title"></label>
                  <input type="text" class="form-control" id="title" v-model="tempProduct.title">
                </div>

                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="category"></label>
                    <input type="text" class="form-control" id="category"
                      placeholder="" v-model="tempProduct.category">
                  </div>
                  <div class="form-group col-md-6">
                    <label for="price"></label>
                    <input type="unit" class="form-control" id="unit"
                      placeholder="" v-model="tempProduct.unit">
                  </div>
                </div>

                <div class="form-row">
                  <div class="form-group col-md-6">
                  <label for="origin_price"></label>
                    <input type="number" class="form-control" id="origin_price"
                      placeholder="" v-model="tempProduct['origin_price']">
                  </div>
                  <div class="form-group col-md-6">
                    <label for="price"></label>
                    <input type="number" class="form-control" id="price"
                      placeholder="" v-model="tempProduct.price">
                  </div>
                </div>
                <hr>
                <div class="form-group">
                  <label for="description"></label>
                  <textarea type="text" class="form-control" id="description"
                    placeholder="" v-model="tempProduct.description"></textarea>
                </div>
                <div class="form-group">
                  <label for="content"></label>
                  <textarea type="text" class="form-control" id="content"
                    placeholder="" v-model="tempProduct.content"></textarea>
                </div>
                <div class="form-group">
                  <div class="form-check">
                    <!-- :true/false-value -->
                    <input class="form-check-input" type="checkbox"
                      id="is_enabled" v-model="tempProduct['is_enabled']"
                      :true-value="1" :false-value="0">
                    <label class="form-check-label" for="is_enabled">
                      
                    </label>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <button type="button" class="btn btn-primary" @click.prevent="updateProduct"></button>
          </div>
        </div>
      </div>
    </div>
    <!-- del modal -->
    <div class="modal fade" id="deleteModal" tabindex="-1" role="dialog"
      aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-danger text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
             <strong class="text-danger">{{ tempProduct.title }}</strong> ()
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <button type="button" class="btn btn-danger" @click.prevent="deleteProduct"></button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from 'jquery'
import Pagination from '../../views/Pagination'
export default {
  components: {
    Pagination
  },
  data () {
    return {
      // 
      products: [],
      // Edit product
      tempProduct: {},
      // 
      isNew: false,
      // modal 
      modalHead: '',
      // Loading Boolen
      isLoading: false,
      // loading
      status: {
        fileUploading: false
      },
      // 
      pagination: {}
    }
  },
  methods: {
    // serverdata
    getProducts (page = 1) { // ES6 
      let vm = this
      let api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/products?page=${page}`
      // Loading
      vm.isLoading = true
      vm.$http.get(api).then((response) => {
        console.log(response.data)
        window.scrollTo(0, 0)
        vm.products = response.data.products
        // pagination
        vm.pagination = response.data.pagination
        // loading
        vm.isLoading = false
      })
    },
    // method,modal
    openModal (isNew, item) {
      if (isNew) { // 
        this.tempProduct = {}
        this.isNew = true
        this.modalHead = ''
      } else { // 
        // ES6 itemobjtempProductitem
        this.tempProduct = Object.assign({}, item)
        this.isNew = false
        this.modalHead = ''
        console.log(this.tempProduct)
      }
      $('#productModal').modal('show')
    },
    //    
    updateProduct () {
      let api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product`
      let vm = this
      let httpMethod = 'post'
      if (!vm.isNew) { // api, 
        api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product/${vm.tempProduct.id}`
        httpMethod = 'put'
      }
      console.log(api)
      // {data: vm.tempProduct}data
      this.$http[httpMethod](api, {data: vm.tempProduct}).then((response) => {
        console.log(response.data)
        if (response.data.success) { // 
          vm.getProducts()
          $('#productModal').modal('hide')
        } else { // 
          $('#productModal').modal('hide')
          alert('')
        }
      })
    },
    // 
    deleteProduct () {
      let vm = this
      let api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product/${vm.tempProduct.id}`
      console.log(api)
      this.$http.delete(api, {data: vm.tempProduct.id}).then((response) => {
        console.log(response.data)
        if (response.data.success) {
          $('#deleteModal').modal('hide')
          vm.getProducts()
        } else {
          $('#deleteModal').modal('hide')
          alert('')
        }
      })
    },
    openDelModal (item) {
      this.tempProduct = Object.assign({}, item)
      $('#deleteModal').modal('show')
      console.log(this.tempProduct)
    },
    // 
    uploadFile () {
      console.log(this)
      const uploadedFile = this.$refs.files.files[0]
      const vm = this
      // FormData 
      const formData = new FormData()
      formData.append('file-to-upload', uploadedFile)
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/upload`
      this.$http.post(url, formData, {
        //  multipart/form-data
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      }).then((response) => {
        console.log(response.data)
        // loading
        this.status.fileUploading = true
        if (response.data.success) {
          // this.$set vm.tempProduct.imageUrlgettersetter
          vm.$set(vm.tempProduct, 'imageUrl', response.data.imageUrl)
        } else {
          // Alert
          this.$bus.$emit('message:push', response.data.message, 'danger')
        }
        // loading
        setTimeout(function () { vm.status.fileUploading = false }, 3000)
      })
    }
  },
  created () {
    this.getProducts()
    // on==> pagination
    this.$bus.$on('page:current', (currentPage) => {
      this.getProducts(currentPage)
    })
  }
}
</script>

<style lang="scss" scoped>
@import "../../../assets/mixin";
.content {
  min-height: calc(100vh - (51px + 62px));
  @include iphone8plus() {
    padding-top: 75px;
  }
}
.create-btn-Pos {
  text-align: right;
  @include iphone8plus() {
    text-align: left;
    margin-left: 10px;
  }
}
.small {
  width: 50px;
}
.table {
  min-width: 800px;
}
</style>



