<template>
  <div class="mt-5">
    <loading :active.sync="isLoading"></loading>
    <h2 class="text-center"></h2>
    <div class="row my-5">
      <div class="col-md-4 mb-4" v-for="item in products" :key="item.id">
        <div class="card border-0 shadow-sm">
          <div style="height: 150px; background-size: cover; background-position: center"
          :style="{backgroundImage: `url(${item.imageUrl})`}">
          </div>
          <div class="card-body">
            <span class="badge badge-secondary float-right ml-2">{{item.category}}</span>
            <h5 class="card-title">
              <a href="#" class="text-dark">{{item.title}}</a>
            </h5>
            <p class="card-text  text-truncate">{{item.content}}</p>
            <div class="d-flex justify-content-between align-items-baseline">
              <div class="h5" v-if="!item.price">{{item.origin_price}}</div>
              <del class="h6" v-if="item.price">{{item.origin_price}}</del>
              <div class="h5" v-if="item.price">{{item.price}}</div>
            </div>
          </div>
          <div class="card-footer d-flex">
            <button type="button" class="btn btn-outline-secondary btn-sm" @click.prevent="getProduct(item.id)"
            data-toggle="modal" data-target="#productModal">
            <!-- v-if  idloading icon -->
              <i class="fas fa-spinner fa-spin" v-if="status.loadingItem === item.id"></i>
              
            </button>
            <button type="button" class="btn btn-outline-danger btn-sm ml-auto" @click="addtoCart(item.id)">
              <i class="fas fa-spinner fa-spin" v-if="status.loadingItem === item.id"></i>
              
            </button>
          </div>
        </div>
      </div>
    </div>
    <!-- Modal -->
    <div class="modal fade" id="productModal" tabindex="-1" role="dialog"
    aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog modal-md" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-dark text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span>{{ product.title }}</span>
            </h5>
            <button type="button" class="close text-white" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="container">
              <div style="height: 150px; background-size: cover; background-position: center"
              :style="{backgroundImage: `url(${product.imageUrl})`}">
              </div>
              <p class="card-text">{{product.content}}</p>
              <p class="text-right">__</p>
              <div class="d-flex justify-content-between align-items-baseline">
                <div class="h5" v-if="!product.price">{{product.origin_price}}</div>
                <del class="h6" v-if="product.price">{{product.origin_price}}</del>
                <div class="h5" v-if="product.price">{{product.price}}</div>
              </div>
              <!-- option -->
              <select class="form-control" v-model="qtySelect">
                <option v-for="num in 10" :value="num" :key="num">
                   {{ num }} {{product.unit}}
                </option>
              </select>
            </div>
          </div>
          <div class="modal-footer">
            <span> {{product.price * qtySelect }}</span>
            <button type="button" class="btn btn-outline-danger btn-sm ml-auto" @click="addtoCart(product.id, qtySelect)">
              <i class="fas fa-spinner fa-spin" v-if="status.loadingItem === product.id"></i>
                
            </button>
          </div>
        </div>
      </div>
    </div>
    <hr>
    <div class="row d-flex justify-content-center my-4" v-if="finalTotal">
      <div class="col-md-6">
        <h3 class="text-center mt-5"></h3>
        <table class="table my-4">
          <thead>
          <!-- width  -->
            <tr>
              <th width="50"></th>
              <th></th>
              <th width="120"></th>
              <th width="120"></th>
            </tr>
          </thead>
          <tbody>
          <!-- eslint v-for -->
            <tr v-for="(item) in cart" :key="item.id">
              <td>
                <button class="btn btn-outline-danger btn-sm mt-2" @click="removeCartItem(item.id)">
                  <i class="far fa-trash-alt"></i>
                </button>
              </td>
              <td>
                <a href="#" class="btn btn-link text-info">
                  <img class="small mr-3 rounded" :src="item.product.imageUrl" alt="">
                  {{ item.product.title }}
                </a>
                <div class="text-success" v-if="item.coupon">
                  
                </div>
              </td>
              <td>
                <div class="mt-2">
                  {{ item.qty }} / {{item.product.unit}}
                </div>
              </td>
              <td class="text-right"><div class="mt-2">{{ item.product.price }}</div></td>
            </tr>
            <tr>
              <td class="text-right" colspan="4">{{ total }}</td>
            </tr>
            <!--  -->
            <tr v-if="finalTotal !== total ">
              <td colspan="2"></td>
              <td class="text-success"></td>
              <td class="text-success text-right">{{ Math.round(finalTotal) }}</td>
            </tr>
          </tbody>
        </table>
        <!--  -->
        <div class="input-group mb-2">
          <input type="text" class="form-control" v-model="coupon_code">
          <div class="input-group-prepend">
            <button class="btn btn-outline-dark" @click="addCouponCode()"></button>
          </div>
        </div>
      </div>
    </div>
    <!--  -->
    <div class="row d-flex justify-content-center my-4">
      <div class="col-10">
        <h3 class="text-center mt-4"></h3>
        <div class="my-5 row justify-content-center">
          <form class="col-md-6" @submit.prevent="createOrder()">
            <div class="form-group">
              <label for="useremail">Email</label>
              <input type="email" class="form-control" name="email" id="useremail"
              v-model="form.user.email" placeholder=" Email" v-validate="'required|email'"
              :class="{'is-invalid':errors.first('email')}" >
              <span class="text-danger">
                {{ errors.first('email') }}
              </span>
            </div>
            <div class="form-group">
              <label for="username"></label>
              <input type="text" class="form-control" name="name" id="username"
              v-model="form.user.name" placeholder=""  v-validate="'required'"
              :class="{'is-invalid':errors.has('name')}">
              <span class="text-danger" v-if="errors.has('name')">
                
              </span>
            </div>
            <div class="form-group">
              <label for="usertel"></label>
              <input type="tel" class="form-control" name="tel" id="usertel" v-model="form.user.tel" placeholder=""
               v-validate="'required'" maxlength="10" :class="{'is-invalid':errors.has('tel')}">
                <span class="text-danger" v-if="errors.has('address')"></span>
            </div>
            <div class="form-group">
              <label for="useraddress"></label>
              <input type="address" class="form-control" name="address" id="useraddress" v-model="form.user.address"
              placeholder="" v-validate="'required'" :class="{'is-invalid':errors.has('address')}">
              <span class="text-danger" v-if="errors.has('address')"></span>
            </div>
            <div class="form-group">
              <label for="useraddress"></label>
              <textarea name="" id="" class="form-control" cols="30" rows="10" v-model="form.message"></textarea>
            </div>
            <div class="text-right">
              <button class="btn btn-danger"></button>
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// modal 
import $ from 'jquery'

export default {
  data () {
    return {
      // 
      products: [],
      // Loading Boolen
      isLoading: false,
      // 
      product: {},
      // loading
      status: {
        loadingItem: ''
      },
      cart: [],
      finalTotal: '',
      total: '',
      // coupon
      coupon_code: '',
      // 
      form: {
        user: {
          name: '',
          email: '',
          tel: '',
          address: ''
        },
        message: ''
      },
      qtySelect: 1
    }
  },
  methods: {
    getProducts (page = 1) { // ES6 
      let vm = this
      let api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/products?page=${page}`
      // console.log(api);
      // Loading
      vm.isLoading = true
      this.$http.get(api).then((response) => {
        console.log('Products', response.data)
        vm.products = response.data.products
        // pagination
        vm.$bus.$emit('page:data', response.data.pagination)
        // vm.pagination = response.data.pagination;
        // loading
        vm.isLoading = false
      })
    },
    getProduct (id) {
      let vm = this
      let api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/product/${id}`
      // console.log(api);
      // loading
      vm.status.loadingItem = id
      this.$http.get(api).then((response) => {
        console.log('product', response.data)
        vm.product = response.data.product
        // num 1==>select
        // modal
        $('#productModal').modal('show')
        // loading
        vm.status.loadingItem = ''
      })
    },
    addtoCart (id, qty = 1) {
      let vm = this
      let api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`
      //  id  qty
      const cart = {
        product_id: id,
        qty
      }
      // loading
      vm.status.loadingItem = id
      this.$http.post(api, {data: cart}).then((response) => {
        console.log('cart', response.data)
        // loading
        vm.status.loadingItem = ''
        // 
        vm.getCart()
        $('#productModal').modal('hide')
      })
    },
    // 
    getCart () {
      const vm = this
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`
      console.log(api)
      vm.isLoading = true
      vm.$http.get(api).then((response) => {
        vm.cart = response.data.data.carts
        vm.finalTotal = response.data.data.final_total
        vm.total = response.data.data.total
        console.log('getCart', response.data)
        vm.isLoading = false
      })
    },
    // 
    removeCartItem (id) {
      const vm = this
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart/${id}`
      console.log(api)
      vm.isLoading = true
      vm.$http.delete(api).then((response) => {
        console.log(response.data)
        vm.isLoading = false
        vm.getCart()
      })
    },
    // 
    addCouponCode () {
      const vm = this
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/coupon`
      // console.log(api);
      const coupon = {
        code: vm.coupon_code
      }
      vm.isLoading = true
      // datacoupnobj
      vm.$http.post(api, { data: coupon }).then((response) => {
        console.log(response.data)
        vm.isLoading = false
        vm.getCart()
      })
      vm.coupon_code = ''
    },
    // 
    createOrder () {
      const vm = this
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/order`
      console.log(api)
      const order = {
        user: vm.form
      }
      vm.isLoading = true
      // 
      this.$validator.validate().then(result => {
        if (result) {
          vm.$http.post(api, {data: order}).then((response) => {
            console.log(response.data)
            // checkout
            if (response.data.success) {
              vm.$router.push(`/customer_checkout/${response.data.orderId}`)
            }
          })
        } else {
          console.log('')
        }
        vm.isLoading = false
      })
    }
  },
  created () {
    this.getProducts()
    this.$bus.$on('page:current', (currentPage) => {
      this.getProducts(currentPage)
    })
    this.getCart()
  }
}
</script>

<style scoped>
.small {
  width: 50px;
}
</style>



