<template>
  <div>
    <!-- loading  -->
    <loading :active.sync="isLoading"></loading>
    <div class="text-right mt-5">
      <button class="btn btn-primary mt-4" @click.prevent="openModal(true)"></button>
    </div>
    <table class="table mt-4">
    <thead>
      <!-- width  -->
      <th></th>
      <th width="200"></th>
      <th width="200"></th>
      <th width="200"></th>
      <th width="100"></th>
      <th width="80"></th>
    </thead>
    <tbody>
    <!-- eslint v-for -->
      <tr v-for="(item) in coupons" :key="item.id">
        <td >{{ item.title }}</td>
        <td>{{ item.percent }}</td>
        <td>{{ item.due_date }}</td>
        <td>
        <!-- is_enableddata -->
          <span v-if="item['is_enabled']" class="text-success"></span>
          <span v-else></span>
        </td>
        <td>
          <button class="btn btn-outline-primary btn-sm" @click.prevent="openModal(false, item)"></button>
        </td>
        <td>
          <button class="btn btn-danger btn-sm"  @click.prevent="openDelModal(item)"></button>
        </td>
      </tr>
    </tbody>
  </table>
  <!-- Modal -->
  <div class="modal fade" id="couponModal" tabindex="-1" role="dialog"
  aria-labelledby="exampleModalLabel" aria-hidden="true">
    <div class="modal-dialog modal-lg" role="document">
      <div class="modal-content border-0">
        <div class="modal-header bg-dark text-white">
          <h5 class="modal-title" id="exampleModalLabel">
            <span>{{ modalHead }}</span>
          </h5>
          <button type="button" class="close" data-dismiss="modal" aria-label="Close">
            <span aria-hidden="true">&times;</span>
          </button>
        </div>
        <div class="modal-body">
          <div class="container">
            <div class="form-group">
              <label for="title"></label>
              <input type="text" class="form-control" id="title" v-model="tempCoupon.title">
            </div>
            <div class="form-group">
              <label for="code"></label>
              <input type="text" class="form-control" id="code" v-model="tempCoupon.code">
            </div>
            <div class="form-group">
              <label for="due"></label>
              <input type="text" class="form-control" id="due" v-model="tempCoupon.due_date">
            </div>
            <div class="form-group">
              <label for="percent"></label>
              <input type="text" class="form-control" id="percent" v-model="tempCoupon.percent">
            </div>
            <div class="form-group">
              <div class="form-check">
               <!-- :true/false-value -->
                <input class="form-check-input" type="checkbox"
                 id="is_enabled" v-model="tempCoupon['is_enabled']"
                 :true-value="1" :false-value="0">
                <label class="form-check-label" for="is_enabled">
                  
                </label>
              </div>
            </div>
          </div>
        </div>
        <div class="modal-footer">
          <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
          <button type="button" class="btn btn-primary" @click.prevent="updateCoupon"></button>
        </div>
      </div>
    </div>
  </div>
    <!-- del modal -->
  <div class="modal fade" id="deleteModal" tabindex="-1" role="dialog"
   aria-labelledby="exampleModalLabel" aria-hidden="true">
    <div class="modal-dialog" role="document">
      <div class="modal-content border-0">
        <div class="modal-header bg-danger text-white">
          <h5 class="modal-title" id="exampleModalLabel">
            <span></span>
          </h5>
          <button type="button" class="close" data-dismiss="modal" aria-label="Close">
            <span aria-hidden="true">&times;</span>
          </button>
        </div>
        <div class="modal-body">
          
          <strong class="text-danger">{{ tempCoupon.title }}</strong>
          ()
        </div>
        <div class="modal-footer">
          <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
          <button type="button" class="btn btn-danger" @click.prevent="deleteCoupon"></button>
        </div>
      </div>
    </div>
  </div>
</div>
</template>

<script>
import $ from 'jquery'
export default {
  data () {
    return {
      // 
      coupons: [],
      // Edit product
      tempCoupon: {},
      // 
      isNew: false,
      // modal 
      modalHead: '',
      // Loading Boolen
      isLoading: false,
      // loading
      status: {
        fileUploading: false
      }
    }
  },
  methods: {
    // serverdata
    getCoupons (page = 1) { // ES6 
      let vm = this
      let api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupons?page=${page}`
      console.log(api)
      // Loading
      vm.isLoading = true
      this.$http.get(api).then((response) => {
        console.log(response.data)
        vm.coupons = response.data.coupons
        // pagination
        vm.$bus.$emit('page:data', response.data.pagination)
        // vm.pagination = response.data.pagination;
        // loading
        vm.isLoading = false
      })
    },
    // method,modal
    openModal (isNew, item) {
      if (isNew) { // 
        this.tempCoupon = {}
        this.isNew = true
        this.modalHead = ''
      } else { // 
        // ES6 itemobjtempCouponitem
        this.tempCoupon = Object.assign({}, item)
        this.isNew = false
        this.modalHead = ''
        console.log(this.tempCoupon)
      }
      $('#couponModal').modal('show')
    },
    //    
    updateCoupon () {
      let api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon`
      let vm = this
      let httpMethod = 'post'
      if (!vm.isNew) { // api, 
        api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon/${vm.tempCoupon.id}`
        httpMethod = 'put'
      }
      console.log(api)
      // {data: vm.tempCoupon}data
      this.$http[httpMethod](api, {data: vm.tempCoupon}).then((response) => {
        console.log(response.data)
        if (response.data.success) { // 
          $('#couponModal').modal('hide')
          vm.getCoupons()
        } else { // 
          $('#couponModal').modal('hide')
          alert('')
        }
      })
    },
    // 
    deleteCoupon () {
      let vm = this
      let api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon/${vm.tempCoupon.id}`
      console.log(api)
      this.$http.delete(api, {data: vm.tempCoupon.id}).then((response) => {
        console.log(response.data)
        if (response.data.success) {
          $('#deleteModal').modal('hide')
          vm.getCoupons()
        } else {
          $('#deleteModal').modal('hide')
          alert('')
        }
      })
    },
    openDelModal (item) {
      this.tempCoupon = Object.assign({}, item)
      $('#deleteModal').modal('show')
      console.log(this.tempCoupon)
    }
  },
  created () {
    this.getCoupons()
    // on==> pagination
    this.$bus.$on('page:current', (currentPage) => {
      this.getCoupons(currentPage)
    })
  }
}
</script>



