<template>
<div>
  <loading :active.sync="isLoading"></loading>
  <div class="my-5 row justify-content-center">
    <form class="col-md-6" @submit.prevent="payOrder">
      <table class="table">
        <thead>
          <tr>
            <th class="text-center"></th>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="item in order.products" :key="item.id">
            <td class="align-middle"><a href="#" class="btn btn-link text-info"><img class="small" :src="item.product.imageUrl" alt="">   {{ item.product.title }}</a></td>
            <td class="align-middle">{{ item.qty }}/{{ item.product.unit }}</td>
            <td class="align-middle text-right">{{ Math.round(item.final_total) | currency }}</td>
          </tr>
        </tbody>
        <tfoot>
          <tr>
            <td class="text-right" colspan="3"> <span>{{ Math.round(order.total) | currency }}</span></td>
          </tr>
        </tfoot>
      </table>
      <table class="table">
        <tbody>
          <tr>
            <th width="100">Email</th>
            <td>{{ order.user.user.email }}</td>
          </tr>
          <tr>
            <th></th>
            <td>{{ order.user.user.name }}</td>
          </tr>
          <tr>
            <th></th>
            <td>{{ order.user.user.tel }}</td>
          </tr>
          <tr>
            <th></th>
            <td>{{ order.user.user.address }}</td>
          </tr>
          <tr>
            <th></th>
            <td>
              <span v-if="!order.is_paid"></span>
              <span v-else class="text-success"></span>
            </td>
          </tr>
        </tbody>
      </table>
      <div class="text-right" v-if="order.is_paid === false">
        <button class="btn btn-danger"></button>
      </div>
    </form>
  </div>
</div>
</template>

<script>
// modal 
export default {
  data () {
    return {
    // routeid
      orderId: '',
      isLoading: false,
      // 
      order: {
        user: {
          user: {} // 
        }
      }
    }
  },
  methods: {
    // 
    getOrder () {
      const vm = this
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/order/${vm.orderId}`
      // console.log(api);
      vm.isLoading = true
      vm.$http.get(api).then((response) => {
        console.log('oneOrder', response.data)
        vm.order = response.data.order
        vm.isLoading = false
      })
    },
    // 
    payOrder () {
      const vm = this
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/pay/${vm.orderId}`
      vm.isLoading = true
      vm.$http.post(api).then((response) => {
        console.log(response.data)
        vm.isLoading = false
        vm.getOrder()
      })
    }
  },
  created () {
    const vm = this
    // 
    vm.orderId = vm.$route.params.orderId
    console.log(vm.orderId)
    // 
    vm.getOrder()
  }
}
</script>

<style scoped>
.small {
  width: 50px;
}
</style>



